"use client";

import { useEffect, useMemo, useState, useCallback } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { API, PORTFOLIO_IMAGE_BASE } from "@/lib/config";

/* =========================
   Lightbox
========================= */
function Lightbox({ images, index, onClose, onPrev, onNext }) {
  if (index < 0) return null;
  const img = images[index];

  useEffect(() => {
    function onKey(e) {
      if (e.key === "Escape") onClose();
      if (e.key === "ArrowRight") onNext();
      if (e.key === "ArrowLeft") onPrev();
    }
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [onClose, onNext, onPrev]);

  return (
    <AnimatePresence>
      <motion.div
        className="fixed inset-0 z-[999] grid place-items-center bg-black/70 backdrop-blur-sm"
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        exit={{ opacity: 0 }}
      >
        <motion.div
          className="relative w-[min(94vw,1200px)] max-h-[90vh] overflow-hidden rounded-2xl border border-white/20 bg-neutral-900 shadow-2xl"
          initial={{ scale: 0.95, y: 20 }}
          animate={{ scale: 1, y: 0 }}
          exit={{ scale: 0.98, y: 10 }}
        >
          <div className="flex items-center justify-center p-4">
            <img
              src={img.src}
              alt={img.alt || "Portfolio"}
              className="max-h-[80vh] w-auto rounded-lg"
            />
          </div>

          <div className="absolute right-4 top-4 flex gap-2 rounded-full bg-black/40 px-2 py-2 ring-1 ring-white/10 backdrop-blur">
            <button
              type="button"
              onClick={onPrev}
              className="rounded-full bg-white/10 px-3 py-1 text-white hover:bg-white/20"
              aria-label="Previous"
            >
              ‹
            </button>
            <button
              type="button"
              onClick={onNext}
              className="rounded-full bg-white/10 px-3 py-1 text-white hover:bg-white/20"
              aria-label="Next"
            >
              ›
            </button>
            <button
              type="button"
              onClick={onClose}
              className="rounded-full bg-gradient-to-r from-fuchsia-500 via-amber-400 to-cyan-400 px-3 py-1 font-semibold text-black"
              aria-label="Close"
            >
              ✕
            </button>
          </div>
        </motion.div>
      </motion.div>
    </AnimatePresence>
  );
}

/* =========================
   Helpers
========================= */
function toPortfolioUrl(raw) {
  if (!raw) return null;
  if (/^https?:\/\//i.test(raw)) return raw;
  const name = String(raw).split(/[\\/]/).pop();
  return `${PORTFOLIO_IMAGE_BASE}${encodeURIComponent(name)}`;
}

function pickArray(json) {
  if (Array.isArray(json?.GetSpecificPortfolio))
    return json.GetSpecificPortfolio;
  if (Array.isArray(json?.data)) return json.data;
  if (Array.isArray(json?.items)) return json.items;
  return [];
}

/* =========================
   Main Client Component
========================= */
export default function ServiceDetailClient({
  serviceId,
  sections,
  initialServiceInfoId,
  initialPortfolio,
  sendQuoteAction,
}) {
  const [activeId, setActiveId] = useState(
    initialServiceInfoId || sections?.[0]?.serviceInfoId || null
  );

  const activeSection = useMemo(
    () => sections.find((s) => s.serviceInfoId === activeId) || null,
    [sections, activeId]
  );

  /* Portfolio */
  const [gallery, setGallery] = useState(initialPortfolio || []);
  const [loading, setLoading] = useState(false);
  const [lightboxIdx, setLightboxIdx] = useState(-1);

  const open = useCallback((i) => setLightboxIdx(i), []);
  const close = useCallback(() => setLightboxIdx(-1), []);
  const next = useCallback(
    () => setLightboxIdx((i) => (i + 1) % (gallery.length || 1)),
    [gallery.length]
  );
  const prev = useCallback(
    () =>
      setLightboxIdx(
        (i) => (i - 1 + (gallery.length || 1)) % (gallery.length || 1)
      ),
    [gallery.length]
  );

  /* Quote */
  const [submitState, setSubmitState] = useState({
    sending: false,
    ok: null,
    error: "",
  });

  const activeTitle = activeSection?.title || "";

  async function onSubmitQuote(e) {
    e.preventDefault();

    try {
      setSubmitState({ sending: true, ok: null, error: "" });

      const form = e.currentTarget;
      const fd = new FormData(form);

      // Ensure correct values always sent
      fd.set("serviceInfoId", String(activeId || ""));
      fd.set("serviceTitle", String(activeTitle || ""));

      const result = await sendQuoteAction(fd);

      if (result?.ok) {
        setSubmitState({ sending: false, ok: true, error: "" });

        // close dialog
        document.getElementById("quoteForm")?.close();

        // reset form
        form.reset();
        return;
      }

      setSubmitState({
        sending: false,
        ok: false,
        error: result?.error || "Failed to send email. Please try again.",
      });
    } catch (err) {
      console.error("[Quote] submit error:", err);
      setSubmitState({
        sending: false,
        ok: false,
        error: "Failed to send email. Please try again.",
      });
    }
  }

  /* Load portfolio when ServiceInfo changes */
  useEffect(() => {
    if (!activeId) return;

    let cancelled = false;
    (async () => {
      try {
        setLoading(true);
        const res = await fetch(API.PORTFOLIO_BY_SERVICEINFO(activeId), {
          cache: "no-store",
        });

        if (!res.ok) {
          if (!cancelled) setGallery([]);
          return;
        }

        const data = await res.json();
        const arr = pickArray(data);

        const images = arr
          .map((x) => {
            const src = toPortfolioUrl(
              x.image || x.Image || x.filename || x.file || x.url || x.src
            );
            return src ? { src, alt: x.title || "Portfolio" } : null;
          })
          .filter(Boolean);

        if (!cancelled) setGallery(images);
      } catch {
        if (!cancelled) setGallery([]);
      } finally {
        if (!cancelled) setLoading(false);
      }
    })();

    return () => {
      cancelled = true;
    };
  }, [activeId]);

  return (
    <main className="bg-neutral-950 text-white">
      <div className="mx-auto max-w-7xl px-4 md:px-8 py-10 grid md:grid-cols-[300px_1fr] gap-6">
        {/* Left Options */}
        <aside className="rounded-2xl border border-white/10 bg-white/5 p-4">
          <h2 className="font-semibold mb-3">Options</h2>

          <ul className="space-y-2">
            {sections.map((s) => {
              const active = s.serviceInfoId === activeId;
              return (
                <li key={s.serviceInfoId}>
                  <button
                    type="button"
                    onClick={() => setActiveId(s.serviceInfoId)}
                    className={`w-full rounded-lg px-3 py-2 text-left transition
                      ${
                        active
                          ? "bg-gradient-to-r from-fuchsia-500 via-amber-400 to-cyan-400 text-black"
                          : "bg-white/5 hover:bg-white/10 text-white/80"
                      }`}
                  >
                    {s.title}
                  </button>
                </li>
              );
            })}
          </ul>

          <button
            type="button"
            onClick={() => document.getElementById("quoteForm")?.showModal()}
            className="mt-4 w-full rounded-full bg-gradient-to-r from-fuchsia-500 via-amber-400 to-cyan-400 py-2 font-semibold text-black"
          >
            Get Quote
          </button>

          <p className="mt-3 text-xs text-white/60">
            Selected option:{" "}
            <span className="text-white/80 font-semibold">
              {activeTitle || "-"}
            </span>
          </p>
        </aside>

        {/* Right Content */}
        <section>
          {activeSection && (
            <article className="rounded-2xl border border-white/10 bg-white/5 p-6">
              <h1 className="text-2xl font-bold">{activeSection.title}</h1>
              <div
                className="prose prose-invert mt-4 max-w-none"
                dangerouslySetInnerHTML={{
                  __html: activeSection.descriptionHtml,
                }}
              />
            </article>
          )}

          {/* Portfolio */}
          <div className="mt-8">
            <h3 className="text-xl font-semibold mb-3">Portfolio</h3>

            {loading && <p className="text-white/60">Loading images…</p>}
            {!loading && gallery.length === 0 && (
              <p className="text-white/60">No portfolio images yet.</p>
            )}

            <ul className="grid grid-cols-2 sm:grid-cols-3 lg:grid-cols-4 gap-4">
              {gallery.map((g, i) => (
                <li key={i}>
                  <button type="button" onClick={() => open(i)}>
                    <img
                      src={g.src}
                      alt={g.alt}
                      className="rounded-xl border border-white/10 aspect-[4/3] object-cover"
                      loading="lazy"
                    />
                  </button>
                </li>
              ))}
            </ul>
          </div>
        </section>
      </div>

      {/* Lightbox */}
      <Lightbox
        images={gallery}
        index={lightboxIdx}
        onClose={close}
        onPrev={prev}
        onNext={next}
      />

      {/* Quote Modal */}
      <dialog
        id="quoteForm"
        className="rounded-2xl p-0 w-[min(92vw,560px)] bg-neutral-950 text-white border border-white/15"
      >
        <div className="p-5 md:p-6">
          <div className="flex items-start justify-between gap-4">
            <div>
              <h3 className="text-xl font-bold">Get a Quote</h3>
              <p className="mt-1 text-white/70 text-sm">
                Service option:{" "}
                <span className="font-semibold text-white">
                  {activeTitle || "-"}
                </span>
              </p>
            </div>

            <button
              type="button"
              onClick={() => document.getElementById("quoteForm")?.close()}
              className="rounded-full bg-white/10 px-3 py-1 text-sm hover:bg-white/20"
            >
              ✕
            </button>
          </div>

          <form onSubmit={onSubmitQuote} className="mt-5 space-y-3">
            {/* hidden fields expected by your server action */}
            <input type="hidden" name="serviceInfoId" value={activeId || ""} />
            <input
              type="hidden"
              name="serviceTitle"
              value={activeTitle || ""}
            />

            <div className="grid gap-3 sm:grid-cols-2">
              <div>
                <label className="text-sm text-white/80">Name *</label>
                <input
                  name="name"
                  required
                  className="mt-1 w-full rounded-lg bg-white/5 border border-white/10 px-3 py-2 outline-none focus:border-white/30"
                  placeholder="Your name"
                />
              </div>

              <div>
                <label className="text-sm text-white/80">Email *</label>
                <input
                  name="email"
                  type="email"
                  required
                  className="mt-1 w-full rounded-lg bg-white/5 border border-white/10 px-3 py-2 outline-none focus:border-white/30"
                  placeholder="you@email.com"
                />
              </div>
            </div>

            <div>
              <label className="text-sm text-white/80">Mobile</label>
              <input
                name="mobile"
                className="mt-1 w-full rounded-lg bg-white/5 border border-white/10 px-3 py-2 outline-none focus:border-white/30"
                placeholder="(306) 000-0000"
              />
            </div>

            <div>
              <label className="text-sm text-white/80">Message</label>
              <textarea
                name="message"
                rows={4}
                className="mt-1 w-full rounded-lg bg-white/5 border border-white/10 px-3 py-2 outline-none focus:border-white/30"
                placeholder="Tell us about your vehicle / project..."
              />
            </div>

            {submitState.ok === false && submitState.error ? (
              <p className="text-sm text-red-300">{submitState.error}</p>
            ) : null}

            <div className="flex items-center justify-end gap-2 pt-2">
              <button
                type="button"
                onClick={() => document.getElementById("quoteForm")?.close()}
                className="rounded-full bg-white/10 px-4 py-2 text-sm hover:bg-white/20"
                disabled={submitState.sending}
              >
                Cancel
              </button>

              <button
                type="submit"
                className="rounded-full bg-gradient-to-r from-fuchsia-500 via-amber-400 to-cyan-400 px-4 py-2 text-sm font-semibold text-black disabled:opacity-60"
                disabled={submitState.sending || !activeId}
                title={!activeId ? "Select an option first" : ""}
              >
                {submitState.sending ? "Sending..." : "Send Quote"}
              </button>
            </div>
          </form>
        </div>
      </dialog>
    </main>
  );
}
